  computed: {
    streetViewList() {
      if (!polyLineInfo.hasOwnProperty(this.listQuery.distIndex)) return {};
      const lineInfoList = polyLineInfo[this.listQuery.distIndex];

      let streetViewList = {};
      for (const lineInfo of lineInfoList) {
        lineInfo.forEach((info, index) => {
          streetViewList[info.fileName] = {
            type: "equirectangular",
            panorama: `${this.panoramaUrl}/${this.listQuery.distIndex}/${info.fileName}.jpg`,
            hfov: 110,
            compass: true,
            northOffset: info.azimuth,
            horizonRoll: 4,
            autoLoad: true,
          };

          let hotSpots = [];
          if (index >= 0 && index < lineInfo.length - 1) {
            hotSpots.push({
              pitch: -5,
              yaw: 0,
              type: "scene",
              text: lineInfo[index + 1].fileName,
              sceneId: lineInfo[index + 1].fileName,
            });
          }

          if (index > 0 && index < lineInfo.length) {
            hotSpots.push({
              pitch: -5,
              yaw: 180,
              type: "scene",
              text: lineInfo[index - 1].fileName,
              sceneId: lineInfo[index - 1].fileName,
              targetYaw: 180,
            });
          }

          streetViewList[info.fileName]["hotSpots"] = hotSpots;
        });
      }

      return streetViewList;
    },
    PCIKMLUrl() {
      // kml網址: ${assetUrl}/kml/PCI/${年月字串}/3_1_${區域號}_${流水號}P${PCI等級}_${單位號}.KML
      // 單位號： 目前都為1 (??)

      const dateStr =
        this.listQuery.year + String(this.listQuery.month).padStart(2, "0");
      let urlObj = {};
      for (const level of [
        ...Object.keys(this.PCILevel),
        ...Object.keys(this.areaList),
      ]) {
        urlObj[
          level
        ] = `${this.assetUrl}/kml/PCI/${dateStr}/3_1_${this.listQuery.distIndex}_P${level}_${kmlFileLastNum[dateStr]}.KML`;
        // urlObj[level] = `${this.assetUrl}/kml/PCI/200002/3_1_${this.listQuery.distIndex}_P${level}_12.KML`;
        // urlObj[level] = `${this.assetUrl}/kml/PCI/202205/3_1_${this.listQuery.distIndex}_P${level}_23.KML`;
      }

      return urlObj;
    },
    caseKMLUrl() {
      // kml網址: ${assetUrl}/kml/PCI/${年月字串}/3_1_${區域號}_${流水號}pointP${PCI等級}_${單位號}.KML
      // 單位號： 目前都為1

      const dateStr =
        this.listQuery.year + String(this.listQuery.month).padStart(2, "0");
      let urlObj = {};
      for (const level of [
        ...Object.keys(this.PCILevel),
        ...Object.keys(this.areaList),
      ]) {
        urlObj[
          level
        ] = `${this.assetUrl}/kml/PCI/${dateStr}/3_1_${this.listQuery.distIndex}_1pointP_${level}_${kmlFileLastNum[dateStr]}.KML`;
      }
      return urlObj;
    },
    districtListFilter() {
      return Object.fromEntries(
        Object.entries(this.districtList).filter(
          ([key, _]) => key != this.listQuery.distIndex
        )
      );
    },
    caseTypeListFilter() {
      return this.listQuery.caseType
        ? Object.fromEntries(
            Object.entries(this.caseTypeList).filter(
              ([key, _]) => key == this.listQuery.caseType
            )
          )
        : this.caseTypeList;
    },
    PCIInfoTotal() {
      const total = this.PCIInfoList.length | 0;
      const areaTotal = this.PCIInfoList.reduce(
        (curr, prev) => (curr += Number(prev.area)),
        0
      );
      const scoreAverage =
        this.PCIInfoList.reduce(
          (curr, prev) => (curr += Number(prev.score)),
          0
        ) / total;

      return { total, areaTotal, scoreAverage: scoreAverage.toFixed(2) };
    },
  },
